import React, { useEffect, useState } from "react";
import { Form, Button, Alert } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import useHttp from "../hooks/useHttp";
import { login } from "../hooks/api";
import { useDispatch , useSelector } from "react-redux";
import { loginHandler } from "../store/AuthSlice";
import styled from "styled-components";
import {useNavigate} from 'react-router-dom'

const FormDiv = styled.div`
  width: 100%;
  margin: auto;

  .custom-btn {
    font-size: 120%;
    font-weight: bold;
    color: orange;
    border-color: orange;
    &:hover {
      background-color: orange;
      color: white;
    }
  }
`;

export default function LoginForm() {
  const [loginUserFunction, loginUserState] = useHttp(login, false);
  const [showAlert, setShowAlert] = useState(false);
  const dispatch = useDispatch();
const navigate = useNavigate()
  const authState = useSelector((state) => state.auth)


  function onSubmitHandler(event) {
    event.preventDefault();

    const username = event.target[0].value;
    const password = event.target[1].value;

    const token = Buffer.from(`${username}:${password}`, "utf8").toString(
      "base64"
    );

    loginUserFunction(token);
  }


  useEffect(() => {
    if (loginUserState.data) {
      dispatch(loginHandler({ token: loginUserState.data.authToken }));
    }
  }, [loginUserState.data, dispatch]);


  const { error } = loginUserState;

  useEffect(() => {
    if (error != null) {
      setShowAlert(true);
    }
  }, [error]);


  const {loggedIn} = authState;

  useEffect(() => {

    loggedIn && navigate("/myCollection")

  },[loggedIn , navigate])

  return (
    <FormDiv>
      <Form
        style={{
          width: "50%",
          margin: "auto",
        }}
        onSubmit={onSubmitHandler}
      >
        <Form.Group className="mb-5">
          <Form.Control
            className="input"
            type="email"
            placeholder="Enter email"
          />
        </Form.Group>

        <Form.Group className="mb-5">
          <Form.Control
            className="input"
            type="password"
            placeholder="Password"
          />
        </Form.Group>
        <Button className="custom-btn" variant="outline-warning" type="submit">
          Submit
        </Button>
      </Form>

      {showAlert && (
        <Alert variant="danger" dismissible onClose={() => setShowAlert(false)}>
          Invalid Credentials
        </Alert>
      )}
    </FormDiv>
  );
}
