import styled from "styled-components";
import { Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import React,{useRef} from "react";


const Form = styled.form`
  text-align: center;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 20px;
  width: 80%;
  margin: auto;

  input {
    width: 80%;
    display: block;
    padding: 10px;
    font-size: 120%;
  }

  textArea {
    width: 90%;
    height: 50vh;
    padding: 20px;
  }

  input,
  textArea {
    margin: auto;
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid white;
    border-radius: 10px;
    outline: none;
    margin-bottom: 30px;
  }
`;

const AddingForm = (props) => {

  const titleRef = useRef();
  const bodyRef =  useRef();


  function onSubmit(event){
    if(!titleRef.current.value){
        titleRef.current.focus()
    }else if(!bodyRef.current.value){
        bodyRef.current.focus();
    }else{
      props.onFormSubmitHandler(event)
    }

  }

  return (
    <Form onSubmit={onSubmit}>
      {props.inputs.map((input, index) => (
        <input ref = {titleRef}
          type={props.type}
          key={index}
          placeholder={input.placeholder}
        ></input>
      ))}
      {props.textArea.map((textArea, index) => (
        <textarea ref = {bodyRef} key={index} cols={textArea.row} rows={textArea.cols} />
      ))}

      <Button className="submit-btn" variant="outline-warning" type="submit">
        {props.buttonText}
      </Button>
    </Form>
  );
};
export default AddingForm;
