import React from "react";
import SinglePoem from "./SinglePoem"
import { useSelector } from "react-redux";
import {LinksContainerGrid} from "../UI/LinksContainerGrid"

export default function Feed() {
  const loadedYet = useSelector((state) => state.poems);

  console.log("feed data")
  console.log(loadedYet);

  return (
    <LinksContainerGrid className="poemsContainer">
              {loadedYet.map((onePoem) => (
                <SinglePoem
                  key={onePoem.id}
                  poem={onePoem}
                ></SinglePoem>
              ))}
            </LinksContainerGrid>
  );
}
