import { useReducer, useCallback } from "react";

function httpReducer(prevState, action) {
  if (action.type === "send") {
    return {
      data: null,
      error: null,
      status: "pending",
    };
  }

  if (action.type === "success") {
    return {
      data: action.responseData,
      error: null,
      status: "complete",
    };
  }

  if (action.type === "error") {
    return {
      data: null,
      error: action.errorMsg,
      status: "complete",
    };
  }

  return prevState;
}

function useHttp(requestFunction, startWithPending = true) {
  const [httpState, dispatch] = useReducer(httpReducer, {
    status: startWithPending ? "pending" : null,
    data: null,
    error: null,
  });

  const sendRequest = useCallback(
    async function (requestData) {
      
      dispatch({ type: "send" });
      
       requestFunction(requestData)
        .then((response) => {
          dispatch({
            type: "success",
            responseData: response.data,
          });
        })
        .catch((err) => {
          console.error(err);
          dispatch({
            type: "error",
            errorMsg: err.message || "Couldnt connect",
          });
        });
    },
    [requestFunction]
  );

  return [sendRequest, httpState];
}

export default useHttp;
