import { createSlice } from "@reduxjs/toolkit";
const initialState = {
  showNavbar: false,
};

const navBarSlice = createSlice({
  name: "showNavbar",
  initialState,
  reducers: {
    showHide(state) {
      state.showNavbar = !state.showNavbar;
    },
  },
});


export const {showHide}  = navBarSlice.actions;
export const navbarReducer = navBarSlice.reducer
