import React from "react";
import { Form, Button, Spinner, Alert } from "react-bootstrap";
import useHttp from "../hooks/useHttp";
import { registerUser } from "../hooks/api";
import "bootstrap/dist/css/bootstrap.min.css";
import styled from "styled-components";

const RegistrationFormDiv = styled.div`
margin:auto;
  width: 100%;
   .custom-btn {
    font-size: 120%;
    font-weight: bold;
    color: orange;
    border-color: orange;
    &:hover {
      background-color: orange;
      color: white;
    }
  }
`;

export default function RegisterForm() {
  const [regUserFunction, regUserState] = useHttp(registerUser, false);

  function onSubmitHandler(event) {
    event.preventDefault();

    const user = {
      penName: event.target[0].value,
      user: {
        username: event.target[1].value,
        password: event.target[2].value,
      },
    };

    regUserFunction(user);
  }

  return (
    <RegistrationFormDiv>
      <Form style={{ width: "50%"  , margin:'auto' , marginBottom :'30px' }} onSubmit={onSubmitHandler}>
        <Form.Group className="mb-5" controlId="formBasicEmail">
          <Form.Control type="text" placeholder="Enter Pen name" />
        </Form.Group>

        <Form.Group className="mb-5" >
          <Form.Control type="email" placeholder="Enter email" />
        </Form.Group>

        <Form.Group className="mb-5" controlId="formBasicPassword">
          <Form.Control type="password" placeholder="Password" />
        </Form.Group>

        <Button variant="outline-warning" className="custom-btn" type="submit">
          {regUserState.status === "pending" && (
            <Spinner
              as="span"
              animation="grow"
              size="sm"
              role="status"
              aria-hidden="true"
            />
          )}
          Submit
        </Button>
      </Form>

      {regUserState.status === "complete" &&
        regUserState.error == null &&
        regUserState.data != null && (
          <Alert styles={{ width: "50%" }} variant="success">
            {regUserState.data}
          </Alert>
        )}

      {regUserState.status === "complete" && regUserState.error != null && (
        <Alert variant="danger">Error Creating User</Alert>
      )}
    </RegistrationFormDiv>
  );
}
