import React from "react";
import styled from "styled-components";
import plus from '../../assets/logos/plus.png'

const AddIcon = styled.img`
&:hover{
    cursor: pointer;
}
position: absolute; 
right:60px;
bottom:60px;
height : 50px;
width: 50px;
&:active{
    transform: scale(0.6);
}

`;

export default function AddButton(props) {

    function onClickHandler() {
        props.setter((prevState)=>!prevState)
    }

  return <AddIcon src={plus} onClick = {onClickHandler}>

  </AddIcon>;
}
