import MoonContainer from "./Components/Structure/MoonContainer";
import React,{useEffect} from "react";
import { ThemeProvider, createGlobalStyle } from "styled-components";
import styled from "styled-components";
import cityImage from "./assets/images/city.jpg";
import "./css/global.css";
import { useSocket } from "./Components/hooks/sock";
import { useDispatch } from "react-redux";
import { setPoems } from "./Components/store/WebSockPoemSlice";


const BackgroundImage = styled.div`
  width: 100%;
  height: 100vh;
  background-image: url(${cityImage});
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: center;
  color: white;
`;

const theme = {
  homePageFont: "Dancing Script, cursive",
  robotoMono: "roboto mono ,monospace",
};

const Global = createGlobalStyle`
body *{
  font-family: ${(props) => props.theme.robotoMono};
  color:white;
}
::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

`;

function App() {
  const dispatch = useDispatch();

  function socketCallback(poemObj) {
    dispatch(setPoems(JSON.parse(poemObj.body)));
  }

  const [connect] = useSocket(socketCallback);

  useEffect(() => {
    connect();
  }, [connect]);

  return (
    <ThemeProvider theme={theme}>
      <Global></Global>
      <BackgroundImage className="background-image">
        <MoonContainer/>
      </BackgroundImage>
    </ThemeProvider>
  );
}

export default App;
