import { createSlice } from "@reduxjs/toolkit";

const initState = []

const loadedPoemsSlice = createSlice({
  name: "loadedPoemsSlice",
  initialState: initState,
  reducers: {
    setPoems(prevState, action) {
      prevState.push(action.payload)
    },
  },
});

export const loadedPoemReducer =  loadedPoemsSlice.reducer;
export const { setPoems} = loadedPoemsSlice.actions;
