import Overlay from "../UI/Overlay";
import styled from "styled-components";
import {useRef} from 'react'
import React from 'react'
const SearchBoxContainer = styled.div`
  width: 100%;
  text-align: center;
`;

const SearchInputBox = styled.input`
margin-top:10px;
  width:40%;
  background-color:black;
  border-radius: 10px;
  padding : 15px;
  border:3px solid white;
  font-size: 120%;
  outline:none;
  color:white;
`;


const Cross = styled.p`
display:inline-block;
&:hover{
    cursor:pointer;
}

margin-left:20px;
transform: scale(1.5, 1.5);

`

export default function SearchBox(props) {

    const searchBoxRef = useRef()

    function onSubmitHandler(event){
        event.preventDefault();
        console.log(searchBoxRef.current.value)

    }

    function crossHandler(){
        props.showSearchModal()
    }

  return (
    <>
      <Overlay  blur={0.8}>
        <SearchBoxContainer>
        <form onSubmit={onSubmitHandler}>
          <SearchInputBox type="text" placeholder="Writers name or lines" ref={searchBoxRef}/>
          <Cross onClick={crossHandler}>&#215;</Cross>
          </form>

        </SearchBoxContainer>
      </Overlay>
    </>
  );
}
