import React, { useState } from "react";
import { Button, Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import SinglePoemCard from "../UI/SinglePoemCard";
import { useSelector } from "react-redux";

export default function SinglePoem(props) {
  const [showPoem, setShowPoem] = useState(false);
  const { loggedIn } = useSelector((store) => store.auth);

  function showPoemHandler() {
    setShowPoem((prev) => !prev);
  }

  function onDeleteHandler() {
    props.deleteHandler(props.poem.id);
  }

  return (
    <SinglePoemCard showPoem={showPoem}>
      <Card className="text-center">
        <Card.Header onClick={showPoemHandler}>{props.poem.title}</Card.Header>
        <Card.Body>
          {showPoem && <Card.Text>{props.poem.body}</Card.Text>}
          {loggedIn && (
            <Button onClick={onDeleteHandler} variant="danger">
              DELETE
            </Button>
          )}
        </Card.Body>
        <Card.Footer className="text-muted">
          {props.poem.creationDate}
        </Card.Footer>
      </Card>
    </SinglePoemCard>
  );
}
