import styled from "styled-components";
import React from 'react'

const Blur = styled.div`
  background-color: rgba(0, 0, 0, ${(props) => props.blur});
  width: 100%;
  height:100vh;
  overflow-y: auto;
`

const Overlay = (props) => {

  return (
    <Blur blur={props.blur} className="myOverlay">
      {props.children}
    </Blur>
  );
};

export default Overlay;
