import { Stomp } from "@stomp/stompjs";
import React from 'react'
var url = "wss://nameless-depths-67341.herokuapp.com/connectUrl";
var stompClient = Stomp.client(url);

 export const useSocket = (socketCallback) => {


  const connect = React.useCallback(() => {
    stompClient.connect(
      {},
      () => {
        console.log("client connected");
        stompClient.subscribe("/topic/posts", socketCallback);
      },
      errorCallback
    );
  },[socketCallback])

  function errorCallback(err) {
    console.log("error in websockets");
  }

  const disconnect = React.useCallback(() => {
    stompClient.disconnect();
  },[])

  return [connect , disconnect];
};


