import axios from "axios";

const apiURL = "https://nameless-depths-67341.herokuapp.com";


export function fetchAllPoemsWithPoetId(poetIdAndToken) {
  
  return axios({
    method: "GET",
    url: `${apiURL}/api/getAllPoemsWithPoetId/${poetIdAndToken[0]}`,
    headers :{
      Authorization: `Bearer ${poetIdAndToken[1]}` 
    }
  });
}

export function addPoem(poemAndToken) {


  return axios({
    method: "POST",
    url: `${apiURL}/api/postPoem`,
    data: poemAndToken[0],
    headers :{
      Authorization: `Bearer ${poemAndToken[1]}` 
    }
  });
}

export function deletePoemWithPoemId(poemIdAndToken) {
  return axios({
    method: "DELETE",
    url: `${apiURL}/api/deletePoemWithPoemId/${poemIdAndToken[0]}`,
    headers :{
      Authorization: `Bearer ${poemIdAndToken[1]}` 
    }
  });
}

export function registerUser(user) {
  
  return axios({
    url: `${apiURL}/register`,
    method: "POST",
    data: user,
  })
}

export function login(token){
  return axios({
    url : `${apiURL}/login` ,
    method : 'POST' ,
    headers : {
      Authorization : `Basic ${token}`
    }
  })
}

//get username , penname , and poetId
export function getProfile(token){
  return axios({
    url : `${apiURL}/api/getProfile` ,
    method : 'GET' ,
    headers : {
      Authorization: `Bearer ${token}`
    }
  })
}


export function getAll(token){
  return axios({
    url : `${apiURL}/api/getAllPoems` , 
    method : 'GET' , 
    headers : {
      Authorization: `Bearer ${token}`  
    }
  })
}