import { createSlice } from "@reduxjs/toolkit";

const initState = {
  loggedIn: false,
  token: null,
};

const authSlice = createSlice({
  name: "authSlice",
  initialState: initState,
  reducers: {
    loginHandler(prevState, action) {
      prevState.loggedIn = true;
      prevState.token = action.payload.token;
      localStorage.setItem('authToken', action.payload.token);
    },

    logoutHandler(prevState, action) {
      prevState.loggedIn = false;
      prevState.token = null;
    },
  },
});

export const authReducer = authSlice.reducer;
export const { loginHandler, logoutHandler } = authSlice.actions;
