import styled from "styled-components";
import React, { useState , useEffect } from "react";
import Header from "./Header";
import Navbar from "./Navbar";
import Overlay from "../UI/Overlay";
import SearchBox from "./SearchBox";
import HomepageHero from "./HomepageHero";
import { useSelector , useDispatch } from "react-redux";
import { Route, Routes , Navigate} from "react-router-dom";
import Allpoems from "./Allpoems";
import About from "./About";
import Login from "../Structure/Login";
import { loginHandler } from "../store/AuthSlice";
import Feed from './Feed'
import { Transition } from "react-transition-group";

const MainContainer = styled.div`
  .widthContainer {
    width: 70%;
    margin: auto;
    height: 90vh;
    display: flex;
    flex-direction: column;
  }
`;


export default function MoonContainer(props) {
  const navbarState = useSelector((state) => state.showHideNavbar.showNavbar);
  const dispatch = useDispatch();
  const [showSearchModal, setShowSearchModal] = useState(false);

  function showSearchModalHandler() {
    setShowSearchModal((prev) => !prev);
  }

  useEffect(()=>{
    const tokenFromMemory = localStorage.getItem('authToken')
    if(tokenFromMemory){
      dispatch(loginHandler({token:tokenFromMemory}))
    }

  },[dispatch])

  return (
    <MainContainer>
      {showSearchModal ? (
        <SearchBox showSearchModal={showSearchModalHandler} />
      ) : (
        <Overlay blur={0.45}>
          <Header showSearchModal={showSearchModalHandler}></Header>

          <div className="widthContainer">
            <Routes>
            <Route path="/" element = {<Navigate replace to="/home"/>}></Route>
              <Route path="/home" element={<HomepageHero/>}/>
              <Route path="/myCollection" element={ <Allpoems/>}/>
              <Route path="/login" element = {<Login/>}/>
              <Route path="/about" element={<About/>}/>
              <Route path="/feed" element={<Feed/>}/>
            </Routes>
          </div>

          <Transition in={navbarState} timeout={500} mountOnEnter unmountOnExit>
            {(state) => <Navbar status={state}></Navbar>}
          </Transition>
        </Overlay>
      )}
    </MainContainer>
  );
}
