import { getAll, addPoem, deletePoemWithPoemId } from "../hooks/api";
import useHttp from "../hooks/useHttp";
import { useSelector  } from "react-redux";
import Loading from "../UI/Loading";
import ErrorPage from "../UI/ErrorPage";
import { LinksContainerGrid } from "../UI/LinksContainerGrid";
import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import AddingForm from "../UI/AddingForm";
import SinglePoem from "./SinglePoem";
import AddButton from "../UI/AddButton";





export default function Allpoems(props) {
  const [allPoems, setAllPoems] = useState([]);

  const authState = useSelector((state) => state.auth);
  const [postPoemFunction, postPoemsState] = useHttp(addPoem, false);
  const [delPoemFunction, delPoemState] = useHttp(deletePoemWithPoemId, false);
  const [getPoemsFunction, getPoemsState] = useHttp(getAll, true);
  const [showAddingForm, setShowAddingForm] = useState(false);
  const { token } = authState;
  const { data } = getPoemsState;



  useEffect(() => {
    data != null && setAllPoems(data);
  }, [data]);

  useEffect(() => {
    token && 
    getPoemsFunction(token)
  }, [getPoemsFunction, token, postPoemsState]);


  function onPoemSubmitHandler(event) {
    event.preventDefault();

    let poemObj = {
      id: null,
      title: event.target[0].value,
      body: event.target[1].value,
      creationDate:null ,
    };

    postPoemFunction([poemObj, token]);


    event.target[0].value = "";
    event.target[1].value = "";
  }


  function onDelHandler(poemId) {
    delPoemFunction([poemId, token]);
    setAllPoems((prevState) => prevState.filter((p) => p.id !== poemId));
  }

  return (
    <>
      {getPoemsState.error != null ||
      postPoemsState.error != null ||
      delPoemState.error != null ? (
        <ErrorPage></ErrorPage>
      ) : (
        <>
          {getPoemsState.status === "pending" ||
          postPoemsState.status === "pending" ||
          delPoemState.status === "pending" ? (
            <Loading></Loading>
          ) : (
            <LinksContainerGrid className="poemsContainer">
              {allPoems.map((onePoem) => (
                <SinglePoem
                  deleteHandler={onDelHandler}
                  key={onePoem.id}
                  poem={onePoem}
                ></SinglePoem>
              ))}
            </LinksContainerGrid>
          )}
        </>
      )}

      {token && showAddingForm && (
        <AddingForm
          onFormSubmitHandler={onPoemSubmitHandler}
          buttonText="Add poem"
          inputs={[{ type: "text", placeholder: "Poem Title" }]}
          textArea={[{}]}
        ></AddingForm>
      )}

      <AddButton setter={setShowAddingForm}></AddButton>
    </>
  );
}
