import { createSlice } from "@reduxjs/toolkit";

const initState = {
  poetId: null,
  username: null,
  penName: null,
};

const profileSlice = createSlice({
  name: "profileInfo",
  initialState: initState,
  reducers: {
    setProfile(prevState, action) {
      prevState.poetId = action.payload.id;
      prevState.penName = action.payload.penName;
      prevState.username = action.payload.username;
    },

    eraseProfile(prevState) {
      prevState.poetId = null;
      prevState.penName = null;
      prevState.username = null;
    },
  },
});

export const profileReducer = profileSlice.reducer;
export const { setProfile, eraseProfile } = profileSlice.actions;
