import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import useLogout from '../hooks/useLogout'
const NavbarContainer = styled.div`
  display: inline-block;
  height: 100%;
  width: 10%;
  position: absolute;
  top: 0;
  left: 0;

  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    ul {
      height: 50%;
      display: flex;
      padding: 0;
      margin: auto;
      flex-direction: column;
      justify-content: space-around;
      list-style: none;
      font-size: 120%;

      .navLink {
        color: white;
        text-decoration: none;
        font-size: 120%;
      }

      .logout-btn{
        background-color:transparent;
        border:none;
        padding:0;
        font-size: 120%;
      }

    }

    

  }

  ${(props) =>
    props.status === "entering" &&
    "animation: navbarAnim 0.6s linear 0s 1 normal"};
  ${(props) =>
    props.status === "exiting" &&
    "animation: navbarAnim 0.6s linear 0s 1 reverse"};

  @keyframes navbarAnim {
    from {
      transform: translateX(-70%);
    }
    to {
      transform: translateX(0);
    }
  }
`;

const linkActive = {
  color: "orange",
};

export default function Navbar(props) {
  const { loggedIn } = useSelector((state) => state.auth);
  const logoutFunction = useLogout()

  return (
    <NavbarContainer status={props.status}>
      <div>
        <ul>
          <li>
            <NavLink
              className="navLink"
              style={({ isActive }) => (isActive ? linkActive : null)}
              to="/home"
            >
              Home
            </NavLink>
          </li>
          <li>
            <NavLink
              className="navLink"
              style={({ isActive }) => (isActive ? linkActive : null)}
              to="/feed"
            >
              Feed
            </NavLink>
          </li>
          {loggedIn && <li>
            <NavLink
              className="navLink"
              style={({ isActive }) => (isActive ? linkActive : null)}
              to="/myCollection"
            >
              Posts
            </NavLink>
          </li>}
          <li>
            <NavLink
              className="navLink"
              style={({ isActive }) => (isActive ? linkActive : null)}
              to="/about"
            >
              About
            </NavLink>
          </li>
          <li>
            {!loggedIn ? (
              <NavLink
                className="navLink"
                style={({ isActive }) => (isActive ? linkActive : null)}
                to="/login"
              >
                Login
              </NavLink>
            ) : (
              <button onClick = {logoutFunction} className="logout-btn">Logout</button>
            )}
          </li>
        </ul>
      </div>
    </NavbarContainer>
  );
}
