import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";

const hero = "MIRAGE";
const subTitle =
  "Mirage is the new age poetry sharing platform for people who feel the words rather than just reading them. Come join us and show everyone how poetic your mind is!";

const HeadingDiv = styled.div`
  text-align: center;
  margin:15% auto;
  h1 {

    font-size: 100px;
  width: 6ch;
  margin:auto auto;
  animation: typing 1s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;


  }


  @keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}

  p {
    text-align: center;
    font-size: 120%;
    margin-bottom: 40px;

  }


  .navLink {
    border: 3px solid orange;
    padding: 7px;
    border-radius: 5px;
    font-size: 120%;
    font-weight: bold;
    color: orange;
    text-decoration: none;
    &:hover {
      background-color: orange;
      color: white;
    }
  }
`;

export default function HomepageHero(props) {
  const { loggedIn } = useSelector((state) => state.auth);

  return (
    <HeadingDiv>
      <h1>{hero}</h1>
      <br></br>
      <p>{subTitle}</p>
      {!loggedIn && (
        <NavLink className="navLink" to="/login">
          Login
        </NavLink>
      )}
    </HeadingDiv>
  );
}
